body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.rounded {
    width: 180px;
    height: 180px;
    position: relative;
    margin: 5px;
    margin-bottom: 30px;
}

.rounded:hover {
    width: 190px;
    height: 190px;
    -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s;
}
.wrapper {
    background: #ECEFF1;
    height: 100%;
    margin: 0;
}
.navbar-brand {
    font-weight: bolder;
    font-size: 40px;
}
.navbar {
    margin-bottom: 50px;
    font-size: 25px;
    font-weight: bold;

}

.alertText, .score {
    padding-top: 20px;
}
