.bodyWrapper {
    width: 70%;
    background-color: white;
    display: flex;
    flex-flow: row wrap;
    padding: 50px;
    padding-bottom: 50px;
    margin-bottom: 100px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
    margin: auto;
}
